import React from 'react'
import { connect } from 'react-redux'
import {deletetodo} from '../../actions'

const buttonDesign={
    backgroundColor:'red',
    border:'none',
    borderRadius:'10px',
    display:'inline-block',
    textDecoration:'none'
    
}

const listStyle={
    backgroundColor: '#5635DF',
    display: 'flex',
    justifyContent: 'space-between',
    borderRadius:'5px',
    color:'white',
    
    
    padding:'10px',
    alignItems: 'center',
    boxShadow: '#ccc 0px 3px 4px',
    width:'210px'
}
const listContainer={
    display: 'flex',
    alignItems: 'center',
    margin:'0 auto',
    marginTop:'10px',
    flexDirection:'column'
  
}
const List = (props) => {
    return (
        <div style={listContainer}>
          {props.todos.map((todo,index)=>{
              
             return <div key={index}style={listStyle}>{todo.message}  <button className={buttonDesign} onClick={()=>
                props.dispatch(deletetodo(todo.id))}>Delete</button></div>
          })  }
        </div>
    )
}
const mapStateToProps =(state)=>({
    todos : state.todos.data,
})

export default connect(mapStateToProps) (List)
